#about {
  background-color: #262626;
  color: #ccc;
  padding: 60px 0;
  position: relative;
  margin: 0 auto;
  width: 80%;
}

#about .subtitle {
  font-family: "Arial Black", Arial, sans-serif;
  font-size: 70px;
  margin-bottom: 10px;
  color: #ccc;
}

 .content-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center; /* Align items vertically */
}

.text-container {
  flex: 0 0 calc(60% - 30px); /* Take up 60% of the container width minus padding */
  max-width: calc(60% - 30px); /* Maximum width */
  padding-right: 50px;
  font-size: 30px;
  line-height: 1.5;
  font-family: Arial, sans-serif;
  text-align: justify;
}

.img-container {
  flex: 0 0 calc(40% - 30px); /* Take up 40% of the container width minus padding */
  max-width: calc(40% - 30px); /* Maximum width */
  padding-left: 50px; /* Add padding to the left */
  display: flex; /* Align items vertically */
  align-items: center; /* Center vertically */
  justify-content: center; /* Center horizontally */
}

.profile-pic {
  width: 600px; /* Set a fixed width */
  height: 600px; /* Set a fixed height */
  border-radius: 50%; /* Make the image round */
  object-fit: cover; /* Cover the container while maintaining aspect ratio */
  overflow: hidden; /* Hide any parts of the image that exceed the circular boundary */
  object-position: center center;
}




.container-hobbies {
  position: relative;
  margin-top: 50px;
  height: 100%;
  display: flex;
  font-size: 30px;
  line-height: 1.5;
  font-family: Arial, sans-serif;
  color: #ccc;
}

.left-side {
  flex: 45%;
}

.fancy-list {
  list-style:disc;
  display:block;
}

.fancy-list li {
  list-style-type: circle;
  padding:10px;
}


.right-side {
  flex: 55%;
  text-align:justify;
  padding-left:100px;
}

.right-side img {
  max-width: 80%;
  height: auto;
}


.vertical-separator {
  position: absolute;
  top: 0;
  left: 45%;
  height: 100%;
  width: 5px;
  background-color: #444;
}



