#projects {
  background-color: #262626;
  color: #ccc;
  padding: 60px 0;
  position: relative;
  margin: 0 auto;
  width: 80%;
}

#projects .subtitle {
  font-family: "Arial Black", Arial, sans-serif;
  font-size: 70px;
  margin-bottom: 10px;
  color: #ccc;
}

.mosaic-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 400px; /* Adjust the square height as needed */
  gap: 20px; /* Adjust the gap between squares as needed */
  width: 1300px; /* Define the width of the mosaic canvas */
  height:900px; /* Define the height of the mosaic canvas */
  margin: 0 auto; /* Center the mosaic container */
  color: #ADD8E6;
  font-size: 25px;
  margin-top:100px;
}



.mosaic-square {
  background-color: #363636; 
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: transform 0.3s ease; /* Add transition for puzzle piece effect */
  color: #bbb;
  font-size: 30px;
  border-radius: 10%; /* Make the image round */
  transition: transform 0.3s ease;
}

.mosaic-square:hover {
  transform: scale(1.05); /* Scale up the square on hover */
}

.mosaic-square.expanded {
  width: 1220px; /* Adjust based on the number of squares per row */
  height: 740px; /* Adjust based on the number of rows */
  position: absolute;
  margin: 0 auto; /* Center the mosaic container */
  z-index: 999;
  padding: 40px; /* Add padding to create space between text and border */
  background-color: #363636;
  color: #bbb;
  border-radius: 5%; /* Make the image round */
  transform:  scale(1); /* adjust scale value as needed */
  transition: transform 0.5s ease;
}

.mosaic-square.expanded:hover{
  transform: scale(1.0);

}


.square-content{
  color: #bbb;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center; /* Align items vertically */
}


.proj-title {
  position: absolute; /* Set position to absolute to position relative to the panel */
  font-size: 38px;
  font-weight: bold;
  top:2cm;
}


.proj-container {
  display: flex;
  width: 100%;
  justify-content: space-between; /* Create space between title and description */
  margin-top: 50px; /* Optional: Adjust margin to separate title from content */
}

.proj-desc {
  flex: 0 0 calc(60% - 10px); /* Take up 60% of the container width minus padding */
  max-width: calc(60% - 10px); /* Maximum width */
  padding-right: 25px;
  font-size: 30px;
  line-height: 1.4;
  font-family: Arial, sans-serif;
  text-align: justify;
}

.proj-img-container {
  flex: 0 0 calc(40% - 10px); /* Take up 40% of the container width minus padding */
  max-width: calc(40% - 10px); /* Maximum width */
  padding-left: 25px; /* Add padding to the left */
  display: flex; /* Align items vertically */
  align-items: center; /* Center vertically */
  justify-content: center; /* Center horizontally */
}


.proj-links{
align-self: flex-start; /* Align to the left */
padding-left: 50px; /* Add padding to the left */
}





.proj-img{
  height: 450px; /* Set a fixed width */
  width: auto; /* Set a fixed height */
  border-radius: 1%; /* Make the image round */
  object-fit: cover; /* Cover the container while maintaining aspect ratio */
  /*overflow: hidden; /* Hide any parts of the image that exceed boundary */
  object-position: center center;
}

.proj-img:hover {
  transform: scale(2.0); /* Scale up the square on hover */
  transition: transform 0.2s ease;
  transition-delay: 0.5s;
}




